import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {RouterModule, Routes} from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';

import { LoginComponent } from './login/login.component';
import { LeavesComponent } from './leaves/leaves.component';
import { MyassetComponent } from './myasset/myasset.component';
import { AddassetComponent } from './addasset/addasset.component';
import { UndeclaredAssetComponent } from './undeclared-asset/undeclared-asset.component';
import { DeclaredAssetComponent } from './declared-asset/declared-asset.component';
import {HolidaysComponent} from './leaves/holidays.component';
import {CancelComponent} from './leaves/cancel.component';
import {LeaveService} from './leaves/leaves.service';
import {LeavedetailsComponent} from './leaves/leavetransaction.component';

const appRoutes: Routes = [
 
  {path:'', component:LoginComponent},
  {path:'home', component:HomeComponent},
  {path:'myasset', component:MyassetComponent},
  {path:'addasset', component:AddassetComponent},
  {path:'leaves', component:LeavesComponent},
  {path:'holidays', component:HolidaysComponent},
  {path:'cancel', component:CancelComponent},
  {path:'transaction', component:LeavedetailsComponent}

];


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    MyassetComponent,
    LoginComponent,
    LeavesComponent,
    AddassetComponent,
    UndeclaredAssetComponent,
    DeclaredAssetComponent,
    HolidaysComponent,
    CancelComponent,
    LeavedetailsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [LeaveService],
  bootstrap: [AppComponent]
})
export class AppModule { }



