import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Router } from '@angular/router';
  import {LeaveService} from './leaves.service';
import * as $ from 'jquery';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Component({
  templateUrl: './cancel.component.html',
  styleUrls: ['./cancel.component.css'],
  providers:[LeaveService]
})
export class CancelComponent implements OnInit{

  Datas:any=[];
  LeaveDatas:any;
  d_Data:any=[];
  deleteDatas:any;
  public checkdata:any;
  public success:any=false;
 private url_deleteLeave = "http://localhost:3000/empId/leaves/leaveId";

  constructor(private router:Router, private leaveService: LeaveService, private http: Http) { }
  
  ngOnInit(){
    this.leaveService.getLeave().subscribe(resData => {this.Datas = resData;this.getLeaveData();console.log(this.Datas)});

  } 
  getLeaveData() {
    this.LeaveDatas = JSON.parse(this.Datas._body).leaves;
      console.log(this.LeaveDatas);

  }
  onSubmit(leavedatas) {
      var length =leavedatas.length;
      console.log(length);
      for(var i=0;i<length;i++){
        console.log(i);
        console.log(leavedatas[i].leaveid);
                var el =  <HTMLInputElement>document.getElementById('check'+leavedatas[i].leaveid);
                //console.log('leaveid' + leavedatas[i].leaveid + '::' + el.checked);
                var leaveid = leavedatas[i].leaveid;
                var empid = leavedatas[i].empid;
                console.log(el);
                if(el != null && el.checked){
                    var url =this.url_deleteLeave.replace('empId',empid).replace('leaveId',leaveid);
                    console.log(url);
                    this.http.delete(url)
                  .subscribe(resData => {this.d_Data = resData;console.log(this.d_Data)});
                }
                //-----------------------disable the checkbox---------------------------
                 if(el != null && this.Datas.status == 200 && el.checked) {
                    console.log(this.Datas.status);
                     var b1 =  <HTMLInputElement>document.getElementById('check'+leavedatas[i].leaveid);
                     b1.disabled =true;
                  }
        }
  }

}



