import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Router } from '@angular/router';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Component({
  templateUrl: './leaves.component.html',
  styleUrls: ['./leaves.component.css']
})
export class LeavesComponent implements OnInit {


  public leaveType;
  public from;
  public to;
  public reason;
  public duration;
  public contact;

  

  constructor( private http: Http, private router:Router) { }
  userdata() {
    let data =
      {
        "leaveType": this.leaveType,
        "From": this.from,
        "To": this.to,
        "Reason": this.reason,
        "Duration": this.duration,
        "Contact": this.contact

      }
    return data;
  }

    ngOnInit() {
     
    var t = this.http.get('http://localhost:3000/leaveTypes').subscribe(res => this.getme(res));
  }

  public k = [];
  getme(data) {
    var g = JSON.parse(data._body);
    this.k = g;
    console.log(this.k);
    return this.k;
  }
  postleaves() {
      console.log(this.userdata());

          var k = JSON.stringify(this.userdata());
    console.log(k);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post('http://localhost:3000/1/leaves', k, options).toPromise()
      .then(res => console.log(res));
  }

  onHolidayClick() {
    this.router.navigate(['/holidays']);
  }
  onApplyClick() {
    this.router.navigate(['/leaves']);
  }
  onCancelClick() {
    this.router.navigate(['/cancel']);
  }
  onTransactionClick() {
    this.router.navigate(['/transaction']);
  }

}



