import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import {LeaveService} from './leaves.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-leavedetails',
  templateUrl: './leavetransaction.html',
  styleUrls: ['./leavetransaction.css'],
    providers:[LeaveService]
})
export class LeavedetailsComponent {

 Datas:any=[];
  LeaveDatas:any;
  d_Data:any=[];
  public checkdata:any;

  public show:any=false;
 private url_getLeave = "http://localhost:3000/2/leaves?startDate=2017-01-01&endDate=2017-12-31";

  constructor(private router:Router, private leaveService: LeaveService, private http: Http) { }

  onGoClick(leavedatas) {
      this.show =true;
      this.http.get(this.url_getLeave)
      .subscribe(resData => {this.Datas = resData;this.getLeaveData();console.log(this.Datas)});
  }

    getLeaveData() {
    this.LeaveDatas = JSON.parse(this.Datas._body).leaves;
      console.log(this.LeaveDatas);

  }
}



